.error {
  font-size: 13px;
  font-weight: 500;
  padding: 3px;
  border-radius: 5px;
  background-color: white;
  color: red;
  width: 212px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 5px;
}

.center {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}

.error_container {
  background-color: #fff;
  padding: 2px;
  border-radius: 2px;
}
.qwe {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  margin-bottom: 10px;
}
