.icon {
  width: 40px;
}

.image_container {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 15px;
  background-color: rgba(255, 255, 255, 1);
  border-radius: 10px;
  transition: 0.5s ease;
  cursor: pointer;
}

.image_container:hover {
  transition: 0.5s ease;
}

.image_container:hover {
  transform: scale(1.2, 1.2);
}

h4 {
  margin-bottom: 0;
}

input,
select {
  color: #95405c !important;
  font-weight: 700;
}
