.row {
  width: 100%;
  display: flex;
  justify-content: space-around;
}

.col {
  display: flex;
  align-items: center;
  justify-content: center;
}
.image_container {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 95px;
  background-color: #fff;
  border-radius: 10px;
  flex-direction: column;
  padding: 8px;
}

.image {
  width: 30px;
}

label {
  font-weight: bold;
  color: rgba(0, 0, 0, 0.98);
  font-size: 13px;
  margin-bottom: 0;
}
