.alert__container {
  display: flex;
  font-weight: bold;
  background-color: #ff9666;
}

.alert__container span {
  white-space: nowrap; /* Отменяем перенос текста */
  overflow: hidden;
  text-overflow: ellipsis; /* Многоточие */
}

.alert__container_bar {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
